*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}
li {
  list-style: none;
 
}
a{
  text-decoration: none;
}

input {
  background: none;
  outline: none;
  border: none;
  color: aliceblue;
  
}
.movieApp{
  
  background: #232b2b;
  

}

/* movie part */
.title {
  color: aliceblue;
  padding: 30px 0;
  text-align: center;
  font-size: 40px;

}
.movies{
  width: 100%;
  position: relative;
 
 
}
.profile {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: pink;
  position: absolute;
  top: 30px;
  right: 30px;
}
.profile>img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  
}
form {
  width: 300px;
  height: auto;
  margin: auto;

 
}
.movie-input {
  
  width: 300px;
   border-bottom: 1px solid white;
   padding-bottom: 10px;
  margin: 50px 0;

}

.movieWrap {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  
  

  
}
.movieWrap>div {
  width: 49%;
  height: 350px;
  padding-left: 1%;
  
}

.movie-card {
  display: flex;
  margin-bottom: 50px;
  
}
.movie-img{
  width: 50%;
  
}
.movie-img>img {
  display: block;
  width: 100%;
  height: 100%;
}
.movie-data{
  width: 50%;
  padding: 0 20px;
  color: white;
}
.movie-title {
  font-size: 20px;
  color: rgb(59, 128, 128);
}
.movie-year{
  font-style: italic;
  margin: 10px 0;
}
.movie-genres{
  margin-bottom: 10px;

}
.movie-summary{
  color: #dddd;
}
.movie-btn>button{
   border: none;
   outline: none;
   width: 100px;
   height: 20px;
   border-radius: 20px;
   margin-top: 20px;
   color: black;
   background-color: rgb(100, 214, 176);
}
.moreBtn {
  border: none;
  background-color: rgb(58, 78, 73);
  color: white;
  width: 200px;
  height: 30px;
  border-radius: 10px;
  outline: none;
  margin: 10px 50%;
  transform: translateX(-60%);
  
  
}
/* detail page */
/* .detail-page{
  display: flex;
  justify-content: space-between;
} */
.detail-img{
  width: 100%;
  height: 100vh;
  position: relative;
}
.detail-img>img{
  display: block;
  width: 100%;
  height: 100%;
}
.detail-date{
  width: 500px;
 position: absolute;
 left: 30px;
bottom: 20%;

}
.detail-title{
  font-size: 60px;
  color: white;
  font-weight: bolder;
}
.detail-summary{
  margin-top: 50px;
  color: rgb(228, 225, 225);
  font-size: 18px;
  line-height: 1.5;

}


